<template>
	<div class="user_wrap">
		<el-row :gutter="10" class="user-top">
			<el-col :span = "5">
				<el-input  placeholder="请输入关键字" prefix-icon="el-icon-search" v-model="searchData.input1"></el-input>
			</el-col>
			
			<el-col :span = "5">
				<el-date-picker
			      	type="daterange"
			      	v-model="searchData.startDate_endData"
			      	start-placeholder="开始时间"
	      		  	end-placeholder="结束时间"
	      		  	value-format="yyyy-MM-dd"
	      		  	style="width:250px">
			    </el-date-picker>
			</el-col>
			<el-col :span= "5">
				<el-select v-model="searchData.role" placeholder="请选择角色" style="width: 100%;">
				    <el-option
				        v-for="item in options"
				        :key="item.value"
				        :label="item.label"
				        :value="item.value">
				    </el-option>
			    </el-select>
			</el-col>
			<el-col :span="5" >
				<el-button icon="el-icon-search" @click = "init"></el-button>
				<el-button type = "primary">批量导出</el-button>
			</el-col>
		</el-row>
	<!--table-->
		<div  style="margin-top:10px;padding: 20px;background: #FFFFFF;">
			<el-row style="padding-bottom: 20px; ">
				<el-col :span="4" :offset="20">
					<el-button type="success" @click="addUserFun">新增人员</el-button>
					<el-button @click = "allDelBut" :disabled="this.multipleSelection.length == 0"> 批量删除</el-button>
				</el-col>
				
			</el-row>
			<!--删除弹框-->
				<el-dialog
				    :visible.sync="delDialogObj.deldialogVisible"
				    width="30%"
				    :before-close="handleClose">
				    <div style="text-align: center;">
				    	<img src="../../assets/del-image/del_image.png"/>
				    	<h3>{{delDialogObj.title}}</h3>
				    	<span>{{delDialogObj.content}}</span>
				    </div>
				    <span slot="footer" class="dialog-footer dialog-footer_span">
					    <el-button @click="delDialogObj.deldialogVisible = false">我在想想</el-button>
					    <el-button type="primary" @click="batchDeleting" v-if="delDialogObj.isAll">确定</el-button>
					    <el-button type="primary" @click="oneDeleteing" v-else>确定</el-button>
				    </span>
				</el-dialog>
				
			<el-row>
				<el-col>
					<el-table ref="multipleTable"  
						:data="tableData3"
						border
					    style="width: 100%"
					    @selection-change="handleSelectionChange">
					    <el-table-column  type="selection" width="55">
					    </el-table-column>
					    
					    <el-table-column  prop="USER_ID" label="编号"  width="120">
					    </el-table-column>
					    <el-table-column prop="USERNAME" label="用户名"   width="120">
					    </el-table-column>
					    <el-table-column prop="UNAME" label="姓名"  >
					    </el-table-column>
					    <el-table-column prop="ROLE_ID" label="角色"  >
					    </el-table-column>
					    <el-table-column prop="EMAIL" label="邮箱"  width="180" show-overflow-tooltip>
					    </el-table-column>
					    <el-table-column prop="LAST_LOGIN" label="最近登录" show-overflow-tooltip >
					    </el-table-column>
					    <el-table-column prop="LAST_IP" label="上次登录IP"  >
					    </el-table-column>
					    <el-table-column  label="操作" >
					    	<template slot-scope="scope">
					    		<el-button type="success" icon="el-icon-edit" @click="editUserFun({id:scope.row.USER_ID})"></el-button>
				     			<el-button type="danger" icon="el-icon-delete" @click="delFun({id:scope.row.USER_ID})"></el-button>
					    	</template>
					    </el-table-column>
					</el-table>
				</el-col>
			</el-row>
			<el-row style="padding-top:20px ;">
				<el-col :span="4" :offset="14">
					<el-pagination 
						@size-change="handleSizeChange"
						@current-change="handleCurrentChange"
			            :current-page="currentPage"
			            :total="organNumber"
			            :page-size="pagesize"		        		
			            layout="total, prev, pager, next, jumper">
					</el-pagination>
				</el-col>
			</el-row>		
		
		
		</div>
	</div>
</template>

<script>
	import userData from './user.js'
	import {
		getUsersPort ,  //系统用户查询
		deletePLUserPost	//删除用户
	} from 'api/systemUser.js'
  	export default {
	    name: 'systemUser',
	    
	    data() {
	      	return {
	      		
	      		tableData3:userData,
	      	    ruleForm:{
		        	menuName:'',
		        	menuRole:'',
		        	menuMail:''
		        },
		        rules:{
		        	menuName:[
		        		{ required: true, message: '请输入菜单名称', trigger: 'blur' },
		            	{ min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
		            ],
		            menuRole:[
		        		{ required: true, message: '请选择角色', trigger: 'change' },
		            ],
		            menuMail:[
		        		{ required: true, message: '请输入邮箱', trigger: 'blur' },
		            ]
		        },
		        delId:'',	//删除行id
	      		//删除弹框
	      		delDialogObj:{
					deldialogVisible:false,
					title:'删除操作',
					content:'删除后不可恢复，确认要删除吗？',
					isAll:false,
				},
				//筛选
		  		searchData:{
		  			input1:'',
		  			startDate_endData:'',
		  			role:''
		  		},
		  		options:[{
		          value: '1',
		          label: '选项1'
		        }, {
		          value: '2',
		          label: '选项2'
		        }, {
		          value: '3',
		          label: '选项3'
		        }],
		        multipleSelection: [],
		        pagesize:5,
		   		currentPage:1,
		   		organNumber:0,
	      	}
	    },
	    computed: {
	    	
	    },
	    created(){
	    	this.init()
	    },
	    
	    methods:{
	  		//初始化
	  		init(){
	  			let resData = {
	  				limit:this.currentPage,    //当前页
	  				offset:this.pagesize ,//条数
					USERNAME:this.searchData.input1,    //登录用户名
					CREATETIME:this.searchData.startDate_endData[0],//开始时间
					LAST_CHANGE:this.searchData.startDate_endData[1],    //结束时间
					ROLE_ID:this.searchData.role 		//角色id
	  			}
//	  			console.log('用户'+JSON.stringify(resData))
	  			getUsersPort(resData).then( res =>{
	  				this.tableData3 = res.ALLUSER
	  				this.organNumber = res.TOTALRESULT
//	  				console.log(JSON.stringify(res))
	  			})
	  		},
	  		//新增人员
	  		addUserFun(){
	  			this.$router.push({
	  				name:'addEditUser_list'
	  			})
	  		},
			
	  		//编辑按钮
	  		editUserFun(obj){
	  			this.$router.push({
	  				name:'addEditUser_list',
	  				params:{id:obj.id}
	  			})
	  		},
			//批量
			allDelBut(){
				this.delDialogObj.deldialogVisible = true;
				this.delDialogObj.isAll = true;
				
			},
			//删除单行
		    oneDeleteing(){
				this.delDialogObj.deldialogVisible = false
				deletePLUserPost({USER_ID:this.delId}).then( res =>{
					console.log('删除单行'+JSON.stringify(res))
					this.init()
				})
			},
			//多选删除确定
			batchDeleting(){
				
				var ids = this.multipleSelection.map(item => item.USER_ID).join();
				this.delDialogObj.deldialogVisible = false	
				console.log(JSON.stringify(ids))
				deletePLUserPost({USER_ID:ids}).then( res =>{
//					console.log('删除'+JSON.stringify(res))
					this.init()
				})
			},
			//单行删除按钮
			delFun(obj){
				this.delId=obj.id;
				this.delDialogObj.deldialogVisible = true
				
			},
			
			//table多选
		    handleSelectionChange(val) {
		        this.multipleSelection = val;
//		       console.log(JSON.stringify(val))
		    },
		    //初始页currentPage、初始每页数据数pagesize和数据data
	    	handleSizeChange: function (size) { 
				this.pagesize = size; 
			}, 
			handleCurrentChange: function(currentPage){ 
				this.currentPage = currentPage; 
				this.init();
			},
			//删除弹框关闭
		    handleClose(done,event) {
				this.$confirm('确认关闭？').then(() => {
					done();
				}).catch(() => {});
			},
		}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	.user_wrap{
		.user-top{
			background: #fff;
			padding: 20px;
			border-radius: 6px;
		}
		
	}
</style>



